import type { IBlackprintBootstrapModules, ClosureModeProps } from "types/Blackprint"

export const define: Function = (closure?: any, mode?: ClosureModeProps) => {

    const isDebugMode = mode === "debug"

    isDebugMode && console.log(closure)

    return closure
}

const build = (): IBlackprintBootstrapModules => {

    const lib = {
        define
    }

    return lib as IBlackprintBootstrapModules
}

export default build